html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 18px;
  line-height: 20.7px;
}

body {
  justify-content: center;
  align-items: center;
  display: flex;
}

.people {
  background: #666;
  grid-template-columns: repeat(2, 200px);
  gap: 40px 30px;
  display: grid;
}

@media (width > 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.person {
  border-radius: 12px 12px 0 0;
  width: 200px;
  transition-property: background-color, transform;
  transition-duration: 1s;
  position: relative;
}

.person__image {
  margin-bottom: 8px;
}

.person__image-photo {
  border-radius: 50%;
  width: 100%;
  height: 100%;
}

.person__image-rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  line-height: 32px;
  display: block;
  position: absolute;
  top: 84px;
  right: 10px;
}

.person__details {
  text-align: center;
}

.person__details-name {
  color: #2f4f4f;
  margin-bottom: 9px;
  font-size: 20px;
  line-height: 23px;
}

.person__extra {
  visibility: hidden;
  color: #000;
  text-align: center;
  padding-bottom: 16px;
  transition-property: background-color;
  transition-duration: 1s;
  position: absolute;
}

.person__extra-sport:after {
  content: ",";
}

.person__extra-pay:before {
  content: "$";
}

.person__extra-pay:after {
  content: "M";
}

.person:hover .person__extra {
  visibility: visible;
  background-color: #eee;
  border-radius: 0 0 12px 12px;
  width: 100%;
}

.person:hover {
  z-index: 1;
  background-color: #eee;
  border-radius: 12px 12px 0 0;
  position: relative;
  transform: scale(1.33);
}
